.RemoteWorkBlock {
    min-height: 10vh;
    background-color: #282927;
    .RemoteWorkContainer {
        display: flex;
        flex-direction: column;
        padding-top: 10px;
        padding-bottom: 10px;
        .title {
            color: white;
            font-weight: bold;

        }
        .btn {
            padding: 10px;
            border-radius: 5px;
            &:hover {
                background-color: #1e1f1d;
                color: white;
            }
        }
    }
}









